import { Ref, ref } from 'vue'
import { SupabaseClient } from '@supabase/supabase-js'
import { ITableData, IFullMunicipioData, INewMunicipioData } from '@/interfaces/ITableData'

class CrudService {
    private client: SupabaseClient
    //TODO: INTERFACES
    private tables = ['Municipios', 'Departamentos', 'Viviendas', 'TipoViviendas', 'Personas', 'Familias','TipoHogar' ]
    //TODO: INTERFACES

    private tableData : ITableData = { 'Municipios': ref([]), 'Personas': ref([]), 'Familias': ref([]), 'Viviendas': ref([]) } // { 'Municipios': [], 'Personas': [], 'Familias': [], 'Viviendas': [] }
    private currentValue = { 'Municipios': ref({}), 'Personas': ref({}), 'Familias': ref({}), 'Viviendas': ref({}) }

    constructor(client : SupabaseClient ,) {
        this.client = client
    }

    fetchDataById = async (table: 'Municipios'|'Personas'|'Familias'|'Viviendas', id: number) => {
        if ((table in this.tables)) {
            console.error('Table not found')
            return
        }
        try {
            const { data, error } = await this.client.from(table).select('*').eq('id', id)
            if (error) {
                console.error(error)
                return
            }
            this.currentValue[table].value = data as IFullMunicipioData[]
        } catch (error) {
            console.error(error)
        }
    }

    getCurrentValue = (table: 'Municipios'|'Personas'|'Familias'|'Viviendas',) => {
        if ((table in this.tables)) {
            console.error('Table not found')
            return
        }
        return this.currentValue[table]
    }

    getTableData = (table: 'Municipios'|'Personas'|'Familias'|'Viviendas') => {
        if ((table in this.tables)) {
            console.error('Table not found')
            return
        }
        return this.tableData[table]
    }

    //TODO: CORREGIR ESTO
    fetchTableData = async (table: 'Municipios'|'Personas'|'Familias'|'Viviendas') => {
        if ((table in this.tables)) {
            console.error('Table not found')
            return
        }
        try {
            const { data, error } = await this.client.from(table).select('*')
            if (error) {
                console.error(error)
                return
            }
            this.tableData[table].value = data as IFullMunicipioData[]
        } catch (error) {
            console.error(error)
        }
    }

    fetchTableDataAll = async () => {
        await this.fetchTableData('Municipios')
        await this.fetchTableData('Personas')
        await this.fetchTableData('Familias')
        await this.fetchTableData('Viviendas')
    }

    insertDataToTable = async (table: 'Municipios'|'Personas'|'Familias'|'Viviendas', data: INewMunicipioData) => {
        if ((table in this.tables)) {
            console.error('Table not found')
            return
        }
        try {
            const { error } = await this.client.from(table).insert([data])
            if (error) {
                console.error(error)
                return
            }
        } catch (error) {
            console.error(error)
        }
    } 

    deleteDataFromTable = async (table: 'Municipios'|'Personas'|'Familias'|'Viviendas', id: number) => {
        if ((table in this.tables)) {
            console.error('Table not found')
            return
        }
        try {
            const { error } = await this.client.from(table).delete().eq('id', id)
            if (error) {
                console.error(error)
                return
            }
        } catch (error) {
            console.error(error)
        }
    }

    
    updateDataInTable = async (table: 'Municipios'|'Personas'|'Familias'|'Viviendas', id: number, data: INewMunicipioData) => {
        if ((table in this.tables)) {
            console.error('Table not found')
            return
        }
        try {
            const { error } = await this.client.from(table).update(data).eq('id', id)
            if (error) {
                console.error(error)
                return
            }
        } catch (error) {
            console.error(error)
        }
    }
    
}

export default CrudService