<template>
    <div class ="card w-50  my-5 mx-auto ">
        <div class="card-header">
             <h4>
                Municipios
                <router-link :to="{name:'create-municipio'}" class="btn btn-primary float-end">Crear municipio</router-link>
            </h4>
        </div>
        <div class="card-body">
            <table-component v-if="showTable" :headers="headers" :data="data" @delete-current-row="deleteRow" @edit-current-row="editRow"></table-component>
        </div>
    </div>
    <div class="w-50  my-5 mx-auto ">
        <router-link class="btn btn-primary my-3 mx-auto" :to="{name:'lugares'}">Volver</router-link>
    </div>
</template>

<script setup lang="ts">
    import TableComponent from '@/components/TableComponent.vue';
    import router from '@/router';
    import { inject, onMounted, ref, Ref } from 'vue';

    const crudService: any = inject('crudService')

    const formData = ref({
        name: ''
    })

    const headers = ["id", "name", "nombre_departamento"]
    const dataRaw = crudService.getTableData('Municipios')
    const departamentos = crudService.getTableData('Departamentos')
    let showTable: Ref<boolean> = ref(false)

    const data = ref([])
    onMounted (
        () => {
            data.value = dataRaw.value.map( (row: any) => {
                return {
                    id: row['id'],
                    name: row['name'],
                    nombre_departamento: departamentos.value.find( (dep: any) => dep['id'] === row['id_departamento'])['name']
                }
            })
        }
    )


    onMounted( () => {
        showTable.value = true
    })

    const createMunicipio = async () => {
        await crudService.insertDataToTable('Municipios', formData.value)
        await crudService.fetchTableData('Municipios')
        formData.value = {
            name: ''
        }
    }

    const deleteRow = async (row: any) => {
        await crudService.deleteDataFromTable('Municipios', row['id'])
        await crudService.fetchTableData('Municipios')
    }

    const editRow = async (row: any) => {
        await crudService.fetchDataById('Municipios', row['id'])
        router.push({name: 'edit-municipio', params: {id: row['id']}})
    }

</script>

