<template>
    <div>
        <div class="card w-50 my-5 mx-auto">
            <div class="card-header">
                <h4>Editar municipio</h4>
            </div>
            <div class="card-body">
                <form>
                    <div class="form-group">
                        <label for="municipio">Municipio</label>
                        <input type="text" class="form-control" id="municipio" placeholder="Municipio" v-model="modeledSelected.name">
                    </div>
                    <button type="submit" class="btn btn-primary my-3" @click.prevent="editMunicipio">Submit</button>
                </form>
            </div>
        </div>
        <div class="w-50  my-5 mx-auto ">
            <router-link class="btn btn-primary my-3 mx-auto" :to="{name:'municipios'}">Volver</router-link>
        </div>
    </div>
</template>

<script setup lang="ts">
    import { useRoute } from 'vue-router';
    import { inject, ref, onMounted } from 'vue';
    import router from '@/router';

    const route = useRoute();
    const id = route.params.id;

    const crudService: any = inject('crudService');

    const modeledSelected = ref({
        name: ''
    });

    onMounted(async () => {
        const placeholderData = await crudService.getCurrentValue('Municipios').value[0];
        if (placeholderData) {
            modeledSelected.value.name = placeholderData.name;
        }
    });

    const editMunicipio = async () => {
        const data = {
            name: modeledSelected.value.name
        };
        console.log(data);
        await crudService.updateDataInTable('Municipios', id, data);
        await crudService.fetchTableData('Municipios');
        router.push({ name: 'municipios' });
    };
</script>

