import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router'

import HomeView from '@/views/HomeView.vue'
import MunicipiosView from '@/views/LugaresViews/MunicipiosView.vue'
import CreateMunicipioView from '@/views/LugaresViews/CreateMunicipioView.vue'
import EditMunicipioView from '@/views/LugaresViews/EditMunicipioView.vue'
import PersonasView from '@/views/PersonasView.vue'
import ViviendasView from '@/views/ViviendasView.vue'
import LugaresView from '@/views/LugaresViews/LugaresView.vue'

const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/viviendas',
    name: 'viviendas',
    component: ViviendasView
  },
  {
    path: '/viviendas/create',
    name: 'create-vivienda',
    component: ViviendasView
  },
  {
    path: '/viviendas/edit/:id',
    name: 'edit-vivienda',
    component: ViviendasView
  },
  {
    path: '/lugares',
    name: 'lugares',
    component: LugaresView
  },
  {
    path: '/lugares/municipios',
    name: 'municipios',
    component: MunicipiosView
  },
  {
    path: '/lugares/municipios/create',
    name: 'create-municipio',
    component: CreateMunicipioView
  },
  {
    path: '/lugares/municipios/edit/:id',
    name: 'edit-municipio',
    component: EditMunicipioView
  },
  {
    path: '/personas',
    name: 'personas',
    component: PersonasView
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
